
svg {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 20vw;
  /* set height to pixels if you want angle to change with screen width */
}
  
  #mycontainer{
  background: linear-gradient(222deg, #14b01a, #a71ae8);
background-size: 400% 400%;
position: relative;

-webkit-animation: AnimationName 7s ease infinite;
-moz-animation: AnimationName 7s ease infinite;
-o-animation: AnimationName 7s ease infinite;
animation: AnimationName 7s ease infinite;
}

@-webkit-keyframes AnimationName {
0%{background-position:0% 50%}
50%{background-position:100% 50%}
100%{background-position:0% 50%}
}
@-moz-keyframes AnimationName {
0%{background-position:0% 50%}
50%{background-position:100% 50%}
100%{background-position:0% 50%}
}
@-o-keyframes AnimationName {
0%{background-position:0% 50%}
50%{background-position:100% 50%}
100%{background-position:0% 50%}
}
@keyframes AnimationName { 
0%{background-position:0% 50%}
50%{background-position:100% 50%}
100%{background-position:0% 50%}
}

.mycontainer{
  width: 100%;
  min-height: 90vh;
  display: flex;
  align-items: center;
margin: 1px 0px
}

body{
  padding:0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif
}

h1{
  color: rgba(255, 255, 255, 0.849);
  padding:  10vmin  15vmin 10vmin 15vmin;
  font-size: calc(20px + 2vmin);
}

.row {
  display:flex;
  flex-direction: column;

  width: 100%;
}
.btn {
  min-width: 100px;
  height:30px;
  border: white 1px solid;
  border-radius: 10px;
  padding:5px 20px;
  display:flex;
  color: white;
  align-items: center;
  margin: 0 15vmin;
  font-size: calc(13px + 1vmin);;
  position: absolute;
  bottom: 25vmin
}
img {
  max-width:100%;
max-height:100%;
}

@media screen and (min-width : 992px){
.cards {
  display:grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 400px;
  grid-gap: 100px;
  margin: 0 100px
}
}
@media screen and (max-width : 992px){
  .cards {
    display:grid;
    grid-template-columns: 1fr;
    grid-auto-rows: 400px;
    margin: 20px 20px
  }
}
.card {
  border-radius: 10px;
  box-shadow:  0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* padding: 15px */
}
.hoveredCard {
  background-image: linear-gradient(2deg, #646664, #474547);
}
.hoveredCardDiv {
  color: white;
  font-weight: bold
}
.card h3 {
  color: white
}
.block{
display: flex;
justify-content: center;
margin: -120px 0 50px 0 ;
}
h2{
color: grey
}



.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.rotate{
  transform: rotate(-30deg);
  background-color: rgb(92, 85, 85);
  color: yellow;
  width: 90%;
  font-size: calc(16px + 2vmin);
  padding: 20px;
  position: absolute;
  bottom: 110px;
  text-align: center
}

#container3 {
  background-color: #616161
}

.divForm {
	align-items: center;
	display: flex;
	height: 100vh;
	justify-content: center;
	width: 100vw;
}

form {
	background: #373737;
	border-radius: 2px;
	padding: 20px;
	width: 80vmin;
}

h4 {
  color: #ffffff;
  text-align: center
}

.form-group {
	display: flex;
	flex-direction: row-reverse;

	padding-bottom: 3px;
  margin-bottom: 5px;
}
	
	input {
		outline: none;
		border: 0;
  padding: 10px;
  margin-top:20px;
		background: white;
		flex: 1 0 auto;
		font-size: 16px;
		font-weight: 300;
		}


[type='submit']  {
	align-items: center;
	background: #8e8e8e;
	border: 0;
	border-radius: 3px;
	color: white;
	display: flex;
	font-size: 16px;
	font-weight: 500;
	justify-content: center;
  margin: 40px 0 0  0;
	outline: none;
	padding: 10px 0 10px 0;
	text-transform: uppercase;
  width: 100%;
}
  
textarea{
  width: 100%;
  padding: 10px 0px 10px 0px;
  font-size: 16px;
  font-weight: 300;
  font-family: Arial, Helvetica, sans-serif;
  margin-top:20px;
}
  
a{
  text-decoration: none;
  color: white
}

a:hover, .btn:hover{
  background-color: white;
  color: rgba(0, 0, 0, 0.568)
}